@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;400&family=Roboto:wght@400;500;700&display=swap);
.posts__author-email>span{cursor:pointer;transition:0.3s}.posts__author-email>span:hover{color:#cf00f8}

.posts__comment-name{margin-bottom:5px;font-size:19px;color:#444}.posts__comment-message{margin-bottom:5px}.posts__comment-email{text-align:right}.posts__comment-email>span{cursor:pointer;transition:0.3s}.posts__comment-email>span:hover{color:#cf00f8}

.posts__comments-list{padding:10px 25px;list-style:none}.posts__item{margin-bottom:10px}

.posts__title{grid-row:1 / 4;padding:10px;background-color:#cf00f8;border-radius:8px 0 0 8px;font-size:24px;color:#fff}.posts__author-post{padding:10px}.posts__message{margin-bottom:8px;font-size:19px}.posts__author{margin-bottom:20px;text-align:right}.posts__line{width:100%;height:2px;background-color:#8a8a8a}

.posts{list-style:none}.posts__post{display:grid;grid-template-columns:200px 1fr;margin-bottom:10px;border:1px solid #8a8a8a;border-radius:8px}

*{padding:0;margin:0;box-sizing:border-box}html{font-family:"Roboto", "Helvetica", sans-serif}.App{padding:20px 150px 0}.App__title{margin-bottom:40px;text-align:center}

